import { _decorator, Component, Node } from "cc";
import axios from "axios";
const { ccclass, property } = _decorator;

export enum walletType {}

export enum walletChain {}

export const loginUrls = {
  firstLoginCheck: "https://api.infinitytest.cc/api/v1/user/auth/first_login",
  tgLogin: "https://api.infinitytest.cc/api/v1/user/auth/tg_login",
  pwdLogin: "https://api.infinitytest.cc/api/v1/user/auth/pwd_login",
};

export interface firstLoginCheckDataType {
  id: number;
}

export interface firstLoginCheckResponseType {
  code: string;
  data: {
    firstLogin: true;
  };
  message: string;
}

export interface tgLoginDataType {
  authDate?: number;
  firstName?: string;
  hash?: string;
  id: number;
  inviteCode?: string;
  lastName?: string;
  loginChannel?: "GAME_LOBBY";
  photoUrl?: string;
  username?: string;
}

export interface tgLoginResponseType {
  code: string;
  data: {
    avatar: string;
    email: string;
    expiration: number;
    gamePoint: number;
    inviteCode: string;
    isNewUser: true;
    isSetPwd: true;
    token: string;
    totalPoint: 0;
    userId: number;
    userName: string;
    walletAddress: string;
    walletChain: "ETH";
    walletType: "METAMASK";
  };
  message: string;
}

export interface pwdLoginDataType {
  accountUserName: string;
  authDate?: number;
  firstName?: string;
  hash?: string;
  id: number;
  inviteCode?: string;
  lastName?: string;
  loginChannel?: "GAME_LOBBY";
  password: string;
  photoUrl?: string;
  username?: string;
}

export interface pwdLoginResponseType {
  code: string;
  data: {
    avatar: string;
    email: string;
    expiration: number;
    gamePoint: number;
    inviteCode: string;
    isNewUser: true;
    isSetPwd: true;
    token: string;
    userId: number;
    userName: string;
    walletAddress: string;
    walletChain: "ETH";
    walletType: "METAMASK";
  };
  message: string;
}

@ccclass("loginModel")
export class loginModel extends Component {
  async start() {
    const isFirst = await this.checkLoginFirst()
    if(isFirst){
        //展示登录页面
    }else{
       await this.tgLogin()
    }
  }

  update(deltaTime: number) {}

  async checkLoginFirst(){
    const isFirstLogin = await axios.post<firstLoginCheckResponseType>(loginUrls.firstLoginCheck, {
        id: 0
    })
    return isFirstLogin.data.data.firstLogin
  }

  async tgLogin(){
    const loginData = await axios.post<tgLoginResponseType>(loginUrls.tgLogin, {
        id: 0
    })

    if(loginData?.data?.data?.token){
        //登录成功
    }
  }


}
